import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/home.vue'
import mens from './views/mens.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/mens',
      name: 'Mens',
      component: Home
    }
  ]
})
